.blogFlex {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  padding: 2rem 0;
}

.blog {
  width: 100%;
  display: flex;
  flex-basis: 60%;
  flex-direction: column;
  overflow: hidden;
  padding: 1rem;
}

.randomBlog {
  width: 100%;
  display: flex;
  flex-basis: 38%;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}
.fixedItems {
  position: fixed;
}

.contents {
  line-height: 2rem;
  text-align: justify;
  font-size: 13px;
}

.contents img {
  width: 100%;
  object-fit: contain;
}

.categories {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}

.categoriesFlex {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.categories h6 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}
.socialMediaContainer {
  width: 5%;
  position: relative;
  left: -10%;
}
.socialMedia {
  display: flex;
  flex-direction: column;
  position: fixed;
}

.socialMedia a {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
  border-radius: 50%;
  border: 1px solid gainsboro;
  margin-bottom: 1rem;
  text-decoration: none;
  color: black;
  transition: 1s ease-in-out;
}

.socialMedia a:hover {
  transform: scale(1.1);
  background: #640ad2;
  color: white;
}

.comment {
  width: 100%;
}
.titleComment {
  width: 100%;
  padding: 0.5rem 2rem;
}
.postFlex {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}
.shareBtn button {
  outline: none !important;
  border: none !important;
  background: rgb(0, 0, 0);
  padding: 1rem 2rem;
  border-radius: 5px;
  color: white;
}

.commentCard {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
}
.userInfo {
  width: 100%;
  display: flex;
  align-items: center;
}

.dp {
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 50%;
}
.dp img {
  width: 100%;
  object-fit: cover;
}
.dpName {
  padding: 0.5rem 1rem;
}
.dpName p {
  font-size: 10px;
}
.spacing {
  width: 50px;
  height: 50px;
}
.userComment {
  width: 100%;
  padding: 0.5rem 1rem;
  display: flex;
}
.userComment em {
  font-size: 13px;
}
